/* Loader */

.loader-bg {
	position: fixed;
	z-index: 9999999;
	width: 100%;
	height: 100%;
	background-color: #fff;
}

.loader {
	border: 0 solid transparent;
	border-radius: 50%;
	width: 150px;
	height: 150px;
	position: absolute;
	top: calc(50vh - 75px);
	left: calc(50vw - 75px);
}

.loader:before,.loader:after {
	content: '';
	border: 1em solid #d5ac63;
	border-radius: 50%;
	width: inherit;
	height: inherit;
	position: absolute;
	top: 0;
	left: 0;
	animation: loader 2s linear infinite;
	opacity: 0;
}

.loader:before {
	animation-delay: .5s;
}

@keyframes loader {
	0%{
		transform: scale(0);
		opacity: 0;
	}
	50%{
		opacity: 1;
	}
	100%{
		transform: scale(1);
		opacity: 0;
	}
}

/* End of Loader */




/* Navigation Bar */

nav {
  background-color: yellow;
}

nav ul li a{
  margin-left: 1em;
  font-size: medium;
  color: #000;
}

nav #red {
  background-color: red;
  box-shadow: 1px 1px 0.05em #000;
  padding-left: 2em;
  padding-right: 2em;
  color: white;
  border-radius: 3px;
}

nav ul li a:hover {
  transform: scale(1.03);
  transition-property: transform;
  transition-duration: 0.1s;
}

nav ul li a:active {
  transform: scale(1);
  transition-property: transform;
  transition-duration: 0.1s;
}

.redline {
  width: 100%;
  height: 3px;
  background-color: red;
}

/* End of Navigation part */


